body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
}

/* app styles */
.App{
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
}

.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d;
}
.navbar h1{
  margin: 10px 0;
}
.book-list{
  margin: 20px;
}
.book-list ul{
  padding: 0;
  list-style-type: none;
}
.book-list li{
  background: #6d3d6d;
  border-radius: 4px;
  cursor: pointer;
  padding: 10px;
  margin: 10px 0;
}
.book-list li:hover{
  text-decoration: line-through;
  opacity: 0.7;
}
.book-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2rem;
}
.book-list .author{
  font-size: .9rem;
  color: #ddd;
}
.empty{
  text-align: center;
  margin: 20px;
}
form{
  padding: 20px;
}
input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
}
button.submit{
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
}
